$(function () {

    let $headerMenuBtn = $('.mobileMenuBtn');

    let timeOut = 40;

    function delay() {
        let timer;
        return function (getFunc, timeOut) {
            clearTimeout(timer);
            timer = setTimeout(getFunc, timeOut);
        }
    }

    let delayer = delay();
    
    function getViewport() {
        let viewportWidth;

        if(typeof $(window).outerWidth() != 'undefined'){
            viewportWidth = $(window).outerWidth();
        }
        else {
            viewportWidth = $('body').outerWidth();
        }

        return {
            width: viewportWidth,
        }
    } // getViewport()

/*В этой переменной будем хранить значение ширины окна браузера. Ширину окна,
в последствии, будем вычислять динамически - при изменении ширины окна браузера.*/
    let viewportWidth;

/*С помощью этой функции будем вычислять ширину окна браузера при ее изменении*/
    let setViewportWidth = function () {
        viewportWidth =  getViewport().width;
    };

    $headerMenuBtn.on('click', function () {
        onClickMenu();
    }); //  $headerMenuBt.click

    function onClickMenu() {
        btnRotate();
        mobileMenuVisible();
    } // onClickMenu()

/*Через добавление класса 'active' запускаем анимацию кнопки меню*/
    function btnRotate() {
        $headerMenuBtn.stop(true).toggleClass('active');
    } // btnRotate()

/*Показываем / скрываем мобильное меню*/
    function mobileMenuVisible() {
        $('.overlay, .mobileMenu').fadeToggle(400);
    } // btnRotate()

/*При динамическом увеличении ширины окна браузера более 860px
будем скрывать мобильное меню*/
    function closeMobile() {
        if(viewportWidth > 860){
            $('.overlay, .mobileMenu, .mobileMenuBtn').css({
                'display':'none'
            });
            $headerMenuBtn.removeClass('active')
        }
        else if(viewportWidth <= 860){
            $headerMenuBtn.css({
                'display':'block'
            });
        }
    }

    $(window).resize(function () {
        delayer(function () {
            setViewportWidth();
            closeMobile();
        },timeOut);

    });
}); // ready