$(document).ready(function () {
    let slider1 = $('.mainSlider');
    if (slider1.length) {
        slider1.slick({
            dots: true,
            autoplay: true,
            autoplaySpeed: 2000,
            speed: 1700,
            fade: true,
            zIndex: 10,
            arrows: true,
            slidesToShow: 1,
            adaptiveHeight: true,
            // centerMode: true,
            responsive: [
                {
                    breakpoint: 1000,
                    settings: {
                        infinite: true,
                        dots: false,
                        arrows: false
                    }
                }
            ]
        });
    }
});

$(document).ready(function () {
    let slSlider = $('.sliderFor'),
        slSlider2 = $('.sliderNav');
    if (slSlider.length > 0 && slSlider2.length > 0) {
        slSlider.slick({
            slidesToShow: 1,
            slidesToScroll: 1,
            arrows: false,
            zIndex: 10,
            fade: true,
            asNavFor: '.sliderNav'
        });
        slSlider2.slick({
            slidesToShow: 3,
            slidesToScroll: 1,
            asNavFor: '.sliderFor',
            dots: false,
            centerMode: false,
            focusOnSelect: true,
            infinite: true,
            responsive: [
                {
                    breakpoint: 430,
                    settings: {
                        arrows: false,
                        slidesToShow: 2,
                    }
                }
            ]
        });
    }
});


$(document).ready(function () {
    let sliderDown = $('.sliderDown');
    if (sliderDown.length > 0) {
        sliderDown.slick({
            dots: false,
            infinite: true,
            autoplay: true,
            autoplaySpeed: 2000,
            speed: 1700,
            arrows: true,
            slidesToShow: 4,
            slidesToScroll: 4,
            centerMode: false,
            responsive: [
                {
                    breakpoint: 1050,
                    settings: {
                        slidesToShow: 3,
                        slidesToScroll: 3,
                        arrows: false,
                    }
                },
                {
                    breakpoint: 700,
                    settings: {
                        arrows: false,
                        slidesToShow: 2,
                        slidesToScroll: 2
                    }
                },
                {
                    breakpoint: 460,
                    settings: {
                        arrows: false,
                        slidesToShow: 1,
                        slidesToScroll: 1
                    }
                }
            ]
        });
    }
});

$(document).ready(function () {
    $('.prodInnerSliderFor').slick({
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: false,
        zIndex: 10,
        fade: true,
        asNavFor: '.prodInnerSliderNav'
    });
    $('.prodInnerSliderNav').slick({
        slidesToShow: 3,
        slidesToScroll: 1,
        asNavFor: '.prodInnerSliderFor',
        dots: false,
        centerMode: false,
        focusOnSelect: true,
        infinite: true,
        responsive: [
            {
                breakpoint: 430,
                settings: {
                    arrows: false,
                    slidesToShow: 3,
                }
            }
        ]
    });
});

