$(function () {

    $('.noveltyItem').on('click', function () {

        /*Присваивание z-index*/
        let indexFor = $(this).parents('.noveltyAll').css({'z-index':'20000',});
        $('.noveltyAll').not(indexFor).css({'z-index':'100',});

        /*Окрытие / скрытие меню в блоках*/
        let novCur = $(this).parents('.noveltyAll').find('.subList').stop(true).slideToggle(500);
        $('.subList').not(novCur).stop(true).slideUp(500);

        /*Анимация галки*/
        let jackdaw = $(this).parents('.noveltyAll').find('.noveltyItem__link>img');

        if(jackdaw.css('transform') == 'none'){
            jackdaw.stop(true).css({'transform':'rotate(-180deg)', 'transition':'transform linear .5s'});
            $('.noveltyItem__link>img').not(jackdaw).stop(true).css({'transform':'none'});
        }
        else{
            jackdaw.stop(true).css({'transform':'none', 'transition':'transform linear .5s'});
        }

    }); // click

}); // ready