$(function () {

    if($('.productionLeft').length > 0){
        runSc();
    }

   function runSc() {
       let $li = $('.menuFix li');
       let $link = $('.menuFix a');

       let viewportWidth;

       let $relativeBlock = $('.productionLeft');
       let $relativeBlockHeight = $relativeBlock.outerHeight();
       // let $relativeBlockWidth = $relativeBlock.outerWidth();
       let $relativeBlockTop = $relativeBlock.offset().top;

       let $fixBlock = $('.menuFix');
       let $fixBlockHeight = $fixBlock.outerHeight();
       let $fixBlockWidth = $fixBlock.outerWidth();

       let $stopTop = $('.stopTop').offset().top - 50;
       let stopPoint = $stopTop - $fixBlockHeight;
       let topInStop = $relativeBlockHeight - $fixBlockHeight - 40;

       /*Получаем ширину окна*/
       function getViewport() {

           let viewportWidth;

           if (typeof $(window).outerWidth() !== 'undefined'){

               viewportWidth = $(window).outerWidth();

           }
           else {
               viewportWidth = $('body').outerWidth();
           }

           return{
               width:viewportWidth,
           }

       } // getViewport()

       let setViewport = function () {
           viewportWidth = getViewport().width;
       };

       function delay () {
           let a = 0;
           return function (b, c){
               clearTimeout(a);
               a = setTimeout(b, c);
           }
       } // delay ()

       let delayer = delay();

       // Позиционирование плавающего блока
       let walkBlock = function() {

           if(viewportWidth <= 1020){
               $fixBlock.css({
                   'position':'static',
               });
               return;
           }

           let $totalTop = $(window).scrollTop();

           let $stopTopNew = $('.stopTop').offset().top - 90;
           let stopPointNew = $stopTopNew - $fixBlockHeight;
           let $relativeBlockTopNew = $relativeBlock.offset().top;
           let $relativeBlockHeightNew = $relativeBlock.outerHeight();
           let topInStopNew = $relativeBlockHeightNew - $fixBlockHeight - 80;

           if( stopPointNew !== stopPoint ||
               $relativeBlockTopNew !== $relativeBlockTop ||
               topInStopNew !== topInStop ){

               stopPoint = stopPointNew;
               /*Вычитаю 10 (расстояние отступа до плавающего меню), что бы не прыгало меню*/
               $relativeBlockTop = $relativeBlockTopNew - 10;
               topInStop = topInStopNew;
           }

           if($totalTop > stopPoint){
               $fixBlock.css({
                   'position':'absolute',
                   'top': topInStop,
                   'width': $fixBlockWidth,
               });
           }
           else if($totalTop >= $relativeBlockTop){
               $fixBlock.css({
                   'position':'fixed',
                   'top': '10px',
                   'width': $fixBlockWidth,
               });
           }
           else{
               $fixBlock.css({
                   'position':'static',
               });
           }
       }; // walkBlock()

       setViewport();
       $(window).resize(function () {
           delayer(function() {
               setViewport();
               walkBlock();
               removeAdd();
           }, 100);
       });

       $(window).on('scroll', function () {
           walkBlock();
       }); // scroll

       /*Отмена работы перехода по ссылке*/
       $link.click(function (e) {
           if($(this).parent().find('ul').length>0){
               e.preventDefault();
           }
       });

       /*Выпадение меню при наведении*/
       $li.hover(
           function () {
               if(viewportWidth > 1020){
                   $('ul:first', this).stop(true).fadeIn(500);
               }
           },
           function () {
               if(viewportWidth > 1020){
                   $('ul:first', this).stop(true).fadeOut(200);
               }
           }
       ); // hover

       /*Выпадение меню -> Аккордион*/
       $('.productionLeft a').on('click', function () {
           let click = $(this);
           let closestUl = click.closest('ul');
           let parallelActiveLinks = closestUl.find('.active');
           let closestLi = click.closest('li');
           let clickStatus = closestLi.hasClass('active');
           let count = 0;

           if(viewportWidth <= 1020){
               closestUl.find('ul').slideUp(function () {
                   if(++count == closestUl.find('ul').length){
                       parallelActiveLinks.removeClass('active');
                   }
               });

               if(!clickStatus){
                   closestLi.children('ul').slideDown();
                   closestLi.addClass('active');
               }
           } // <= 1200
       }); // click\
       removeAdd();
       function removeAdd() {

           if(viewportWidth <= 1020){
               $('.productionLeft ul').prev('a').parent().addClass('yesAF');
           }
           else{
               $('.productionLeft ul').prev('a').parent().removeClass('yesAF');
           }
       }
   }
}); // fu()

